import React, { Component, Fragment } from "react"
import "./styles/ficha.css"

class Item extends Component {
    componentDidMount() {
    }
    render() {
        if (this.props.name === "nombre") {
            return (null)
        }
        else {
            if (this.props.name === "telefono" || this.props.name === "direccion") {
                {
                    return (
                        <div className="container color-white bg-dark p-2">
                            <div className="row">
                                <div class="col-12">
                                    <h3>{this.props.name}</h3>
                                </div>
                            </div>
                            <div className="row">
                                {Object.keys(this.props.data).map(key => {
                                    return (
                                        <div className="col-12 text-center">
                                            <h3>{this.props.data[key]}</h3>
                                        </div>
                                    )
                                })}
                            </div>
                        </div>
                    )
                }
            }
            return (
                <div className="container ficha bg-dark p-2">
                    <h3>{this.props.name}</h3>
                    <h3>{this.props.data}</h3>
                </div>
            )
        }
    }
}
export default Item